.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #000000;
  border-radius: 4px;
  background: #16c484;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ffffff;
  color: #16c484;
  border-color: #ff4382;
}

.button--inverse {
  background: transparent;
  color: #1cc864;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #14c188;
}

.button--danger {
  background: #ffffff;
  border-color: #830000;
  color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #ffffff;
  color: #ffffff;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}